import React from 'react'
import Header from './components/header'
import Card from './components/card'
import About from './components/about'
import Footer from './components/footer'
import Serv from './components/services'
import Services from '../src/servicesList'
import skincare from './skinCareProducts'
import Contact from './components/contact'


function makeCard(item) {
  return <Card
    key={item.key}
    name={item.name}
    desc={item.desc}
    price={item.price}
    img={item.img}

  />
}

function make(servis){
  return <Serv
    key = {servis.id}
    name = {servis.name}
    desc = {servis.desc}
    duration = {servis.duration}
    price = {servis.price}
 />
}

function App() {

  return (
    <div> 
      <Header />
      <div id='shop'>
        <h1 className = 'section-name'>STORE</h1>
        <div className='row d-flex justify-content-around' >
          {skincare.map(makeCard)}
        </div>
      </div> 
      <div id='services'>
      <div className='row services d-flex justify-content-center'>
        <h1 className='section-name'>Services</h1>
      {Services.map(make)}
      </div>
      </div>
      <About />
      <div id='contact'>
        <h1 className='section-name' >Contact</h1>
      <Contact />
      </div>
      <Footer />
    </div>
  );
}

export default App;
